import React from "react";
import { connect } from "react-redux";
import { playerCount } from "../../Store/Reducer";
import axios from "axios";
import "./main.css";

export class main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      playersData: [],
    };
  }
  async componentDidMount() {
    const data = async () => {
      try {
        let playersData = await axios.get(
          "https://nodeassignment1.herokuapp.com/getplayers"
        );
        return playersData.data;
      } catch (e) {
        return e;
      }
    };

    let res = await data();
    this.setState({
      playersData: res,
    });
  }

  Previous = () => {
    let prev = this.props.currentVal.currentPlayer;
    if (prev !== 0) {
      prev -= 1;
      this.props.playerCount(prev);
    }
  };

  Next = () => {
    let next = this.props.currentVal.currentPlayer;
    if (next < this.state.playersData.length - 1) {
      next += 1;
      this.props.playerCount(next);
    }
  };

  render() {
    let currentPlayerCount = Number(this.props.currentVal.currentPlayer);
    if (this.state.playersData.length) {
      return (
        <>
          <div className="prv-nxtSec">
            <button
              className={
                currentPlayerCount !== 0
                  ? "PrevNxtButton"
                  : "unClickPrevNxtButton"
              }
              onClick={this.Previous}
            >
              Previous
            </button>
            <button
              className={
                currentPlayerCount < this.state.playersData.length - 1
                  ? "PrevNxtButton"
                  : "unClickPrevNxtButton"
              }
              onClick={this.Next}
            >
              Next
            </button>
          </div>
          <div className="OuterDiv">
            <hr />
            <div className="Player-Section">
              <img
                className="imgDiv"
                src={this.state.playersData[currentPlayerCount].playerImage}
                alt={this.state.playersData[currentPlayerCount].playerName}
              />
              <div className="detailsDiv">
                <p>
                  <span className="headingtext">Name : </span>
                  {this.state.playersData[currentPlayerCount].playerName}
                </p>
                <p>
                  <span className="headingtext">Description : </span>
                  {this.state.playersData[currentPlayerCount].description}
                </p>
                <p>
                  <span className="headingtext">Price : </span>
                  {this.state.playersData[currentPlayerCount].price}
                </p>
              </div>
            </div>
          </div>
        </>
      );
    } else {
      return "Loading";
    }
  }
}

const mapStateToProps = (state) => ({
  currentVal: state.reducer,
});

const mapDispatchToProps = (dispatch) => ({
  playerCount: (payload) => dispatch(playerCount(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(main);
